/* Registration.css */

.container {
  max-width: 400px; /* Adjust the maximum width as needed */
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.form-header {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

/* Add more styles as needed */
.form {
    display: flex;
    flex-direction: column;
    align-items: start baseline; 
}
.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="password"] {
  font-size: 16px;
  width: 100%;
  border-color: orange;
}

.register{
    font-size: 14px;
    padding: 8px 20px;
    margin-top: 10px;
    align-self: center;
    border-color: violet;
    background-color: none;
}