/* Login.css */

.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.form-header {
  font-size: 24px;
  margin-bottom: 20px;
}

.form {
  display: flex;
}

.form-group {
  margin-bottom: 15px;
  width: 100%;
}

.form-group input[type="email"],
.form-group input[type="password"] {
  font-size: 16px;
  width: 100%;
}

.button {
  font-size: 16px;
  padding: 10px;
  margin-top: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100px;
  align-self: center;

  /* Hover effect */
  &:hover {
    background-color: #0056b3;
  }
}

.button a {
  text-decoration: none;
  color: #fff;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.link-text {
  margin-top: 10px;
}

.link-text a {
  text-decoration: none;
  color: #007bff;
}

.link-text a:hover {
  text-decoration: underline;
}
